import React from 'react'
import { useRouter } from '@happysanta/router'
import {
   Avatar,
   Card,
   CardGrid,
   Group,
   NavIdProps,
   Panel,
   PanelHeader,
   PanelHeaderSubmit,
   SimpleCell,
} from '@vkontakte/vkui'
import { PanelHeaderSeparator } from 'components/ui/PanelHeaderSeparator/PanelHeaderSeparator'
import { PAGE_SETTINGS } from 'router/pages'
import { Icon28Settings } from '@vkontakte/icons'

export const PanelOptions: React.FC<NavIdProps> = ({ nav }) => {
   const router = useRouter()

   const onClickSubmit = () => {
      router.popPage()
   }
   return (
      <Panel nav={nav}>
         <PanelHeader separator={false} after={<PanelHeaderSubmit onClick={onClickSubmit} />}>
            
         </PanelHeader>
         <PanelHeaderSeparator />
         <Group>
            <Group mode='plain' separator='hide'>
               <CardGrid size='l'>
                  <Card>
                     <SimpleCell
                        before={
                           <Avatar size={32} mode='image' shadow={false} className='AvatarIcon'>
                              <Icon28Settings width={24} height={24} fill='white' />
                           </Avatar>
                        }
                        expandable
                        onClick={() => {
                           router.pushPage(PAGE_SETTINGS)
                        }}
                     >
                        
                     </SimpleCell>
                  </Card>
               </CardGrid>
            </Group>
         </Group>
      </Panel>
   )
}
