import React from 'react'
import { useAppDispatch, useAppSelector } from 'hooks/redux'

import { CellsRow } from '../CellsRow/CellsRow'

import { SquareCell } from '../SquareCell/SquareCell'
import { SquareCellLetter } from './SquareCellLetter'
import { ITopCell, moveLetterDown, setSelectedCellId, TypeGameSolution } from 'store/slices/state'

const getNewCellId = (cells: ITopCell[]): string | null => {
   let id: string | null = null

   for (const cell of cells) {
      if (cell.currentLetter === null) {
         id = cell.id
         break
      }
   }

   return id
}
const checkSolution = (cells: ITopCell[]) => {
   let full = true
   let correct = true

   let solution: TypeGameSolution = null

   for (const cell of cells) {
      if (cell.currentLetter === null) {
         full = false
         break
      } else if (cell.currentLetter.value !== cell.letter.value) {
         correct = false
      }
   }

   if (full) {
      solution = correct ? 'positive' : 'negative'
   }

   return solution
}

export const CellsRowTop: React.FC = () => {
   const dispatch = useAppDispatch()

   const cells = useAppSelector((state) => state.game.cells.top)
   const selectedCellId = useAppSelector((state) => state.game.selectedCellId)

   React.useEffect(() => {
      const id = getNewCellId(cells)
      const solution = checkSolution(cells)

      console.log('useEffect CellsRowTop', { id, solution })

      dispatch(setSelectedCellId(id))
   }, [cells, dispatch])

   const onClickSelectCell = (id: string) => {
      console.log('top ', id)
      dispatch(setSelectedCellId(id))
   }

   const onClickMoveDown = (id: string) => {
      console.log('move', id)
      dispatch(moveLetterDown(id))
   }

   return (
      <CellsRow className='CellsRow--top'>
         {cells.map((cell) => {
            const key = cell.id

            if (cell.currentLetter !== null) {
               return (
                  <SquareCellLetter
                     onClick={onClickMoveDown}
                     id={cell.id}
                     key={key}
                     color={cell.currentLetter.color}
                     value={cell.currentLetter.value}
                  />
               )
            } else {
               return (
                  <SquareCell
                     onClick={onClickSelectCell}
                     id={cell.id}
                     key={key}
                     mode='outline'
                     selected={cell.id === selectedCellId}
                  />
               )
            }
         })}
      </CellsRow>
   )
}
