import React from 'react'
import { Card, CardProps, classNames } from '@vkontakte/vkui'

import './SquareCell.css'

interface SquareCellProps {
   id: string
   onClick?: (id: string) => void
   /**
    * @param shadow -    
    * @param outline -   
    */
   mode?: Exclude<CardProps['mode'], 'tint'>
   transparent?: boolean
   selected?: boolean
}

export const SquareCell: React.FC<SquareCellProps> = React.memo(
   ({ children, id, onClick = () => {}, transparent, selected, mode = 'outline', ...restProps }) => {
      const onClickCell = () => {
         onClick(id)
      }
      return (
         <Card
            {...restProps}
            onClick={onClickCell}
            className={classNames(
               'SquareCell',
               selected && 'SquareCell--selected',
               transparent && 'SquareCell--transparent'
            )}
            mode={mode}
         >
            {children}
         </Card>
      )
   }
)
