import React from 'react'
import { classNames, Tappable } from '@vkontakte/vkui'

import './RoundWrapIcon.css'

interface RoundWrapIconProps extends React.HTMLAttributes<HTMLDivElement> {
   /**
    * @param s - 48x48
    * @param m - 56x56
    *
    * @default s
    */
   size?: 's' | 'm'
}

export const TappableRoundIcon: React.FC<RoundWrapIconProps> = ({ children, className, size = 's', ...restProps }) => {
   return (
      <Tappable
         activeMode='opacity'
         hoverMode='opacity'
         className={classNames('RoundWrapIcon', `RoundWrapIcon--sz-${size}`, className)}
         {...restProps}
      >
         {children}
      </Tappable>
   )
}
