import React from 'react'
import { classNames, Title } from '@vkontakte/vkui'
import { useAppSelector } from 'hooks/redux'

import './RiddleDescription.css'

export const RiddleDescription: React.FC = () => {
   const description = useAppSelector((state) => state.game.riddle.description)

   return (
      <div className={classNames('RiddleDescription')}>
         <Title level='2' weight='3' className={classNames('RiddleDescription__in')}>
            {description.toUpperCase()}
         </Title>
      </div>
   )
}
