import { GameCells, IBottomCell, ICell, IGame, ILetter, ITopCell, Riddle } from 'store/slices/state'

export enum LetterColorEnum {
   BLUE = 'blue',
   GREEN = 'green',
   ORANGE = 'orange',
   PURPLE = 'purple',
   RED = 'red',
   VIOLET = 'violet',
}

// const pickedColors = new Map<number, boolean>()

const Colors: { [key: number]: LetterColorEnum } = {
   1: LetterColorEnum.BLUE,
   2: LetterColorEnum.GREEN,
   3: LetterColorEnum.ORANGE,
   4: LetterColorEnum.PURPLE,
   5: LetterColorEnum.RED,
   6: LetterColorEnum.VIOLET,
}

export const populateGame = (riddle: Riddle, level: number) => {
   const letters = populateLetters(riddle.word)

   const shuffledLetters = shuffleArray(letters)

   const cells = populateCells(letters, shuffledLetters)

   const game: IGame = {
      attemptNumber: 1,
      level,
      gameMode: 'standard',
      riddle,
      cells,
      letters,
      selectedCellId: null,
      solution: null,
   }

   return game
}

const populateCells = (letters: ILetter[], shuffledLetters: ILetter[]): GameCells => {
   const cells: GameCells = { top: [], bottom: [] }

   cells.top = letters.map((letter, idx) => {
      const cell: ITopCell = {
         letter,
         currentLetter: null,

         id: genCellId(idx, 0),
      }
      return cell
   })

   cells.bottom = shuffledLetters.map((letter, idx) => {
      const cell: IBottomCell = {
         letter,
         currentLetter: letter,
         id: genCellId(idx, 1),
      }
      return cell
   })

   return cells
}

const genCellId = (idx: number, row: number) => {
   return `cell-${row}-${idx}`
}

const populateLetters = (word: string): ILetter[] => {
   const splitted = word.split('')

   const colors = getColors(splitted.length)

   const letters: ILetter[] = splitted.map((_letter, idx) => {
      const letter: ILetter = {
         id: genLetterId(_letter, idx),
         value: _letter,
         color: colors[idx],
      }

      return letter
   })

   return letters
}

const genLetterId = (letter: string, idx: number) => {
   return `letter-${letter}-${idx}`
}

const getColors = (length: number) => {
   const result: LetterColorEnum[] = []

   const colors = Object.values(Colors)

   const _colors = shuffleArray(colors)

   while (result.length < length) {
      if (_colors.length) {
         const color = _colors.pop()
         if (color !== undefined) {
            result.push(color)
         }
      } else {
         const color = colors[Math.floor(Math.random() * colors.length)]
         result.push(color)
      }
   }

   return result
}

