export const shuffleArray = <T>(array: T[]): T[] => {
   return [...array].sort(randomSort)

   // const shuffledArray = [...array]
   // shuffledArray.sort(randomSort)
   // return shuffledArray
}

export const randomSort = () => Math.random() - 0.5

export const getRandomBetween = (min: number, max: number) => Math.floor(Math.random() * (max - min + 1)) + min

export const guessTheWordTitle = [populateLetters(''), populateLetters('')]
