import React from 'react'
import { classNames } from '@vkontakte/vkui'

import './CellsRow.css'

interface CellsRowProps extends React.AllHTMLAttributes<HTMLDivElement> {}

export const CellsRow: React.FC<CellsRowProps> = ({ children, className, ...restProps }) => {
   return (
      <div className={classNames('CellsRow', className)} {...restProps}>
         <div className='CellsRow__in'>{children}</div>
      </div>
   )
}
