import React from 'react'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { moveLetterTop } from 'store/slices/state'
import { CellsRow } from '../CellsRow/CellsRow'
import { SquareCell } from '../SquareCell/SquareCell'
import { SquareCellLetter } from './SquareCellLetter'

export const CellsRowBottom: React.FC = () => {
   const dispatch = useAppDispatch()

   const cells = useAppSelector((state) => state.game.cells.bottom)

   const onClickBottomCell = (id: string) => {
      console.log('bottom ', id)
      dispatch(moveLetterTop(id))
   }

   return (
      <CellsRow className='CellsRow--bottom'>
         {cells.map((cell) => {
            if (cell.currentLetter !== null) {
               return (
                  <SquareCellLetter
                     onClick={onClickBottomCell}
                     id={cell.id}
                     key={cell.id}
                     color={cell.currentLetter.color}
                     value={cell.currentLetter.value}
                  />
               )
            } else {
               return <SquareCell id={cell.id} key={cell.id} mode='shadow' transparent />
            }
         })}
      </CellsRow>
   )
}
