import React from 'react'
import {
   Card,
   CardGrid,
   Group,
   NavIdProps,
   Panel,
   PanelHeader,
   PanelHeaderBack,
   PanelHeaderSubmit,
   Separator,
   SimpleCell,
   Switch,
} from '@vkontakte/vkui'
import { useRouter } from '@happysanta/router'
import { PanelHeaderSeparator } from 'components/ui/PanelHeaderSeparator/PanelHeaderSeparator'

export const PanelSettings: React.FC<NavIdProps> = ({ nav }) => {
   const router = useRouter()

   const onClickBack = () => {
      router.popPage()
   }
   const onClickSubmit = () => {
      router.popPageTo(-2)
   }

   return (
      <Panel nav={nav}>
         <PanelHeader
            separator={false}
            before={<PanelHeaderBack onClick={onClickBack} />}
            after={<PanelHeaderSubmit onClick={onClickSubmit} />}
         >
            
         </PanelHeader>
         <PanelHeaderSeparator />
         <Group>
            <Group mode='plain' separator='hide'>
               <CardGrid size='l'>
                  <Card mode='tint'>
                     <SimpleCell disabled after={<Switch />}>
                        
                     </SimpleCell>
                     <Separator wide />
                     <SimpleCell disabled after={<Switch />}>
                        
                     </SimpleCell>
                     <Separator wide />
                     <SimpleCell disabled after={<Switch defaultChecked />}>
                         
                     </SimpleCell>
                  </Card>
               </CardGrid>

               <CardGrid size='l'>
                  <Card mode='tint'>
                     <SimpleCell indicator=''> </SimpleCell>
                     <Separator wide />
                     <SimpleCell disabled after={<Switch />}>
                         
                     </SimpleCell>
                  </Card>
               </CardGrid>
            </Group>
         </Group>
      </Panel>
   )
}
