import React from 'react'
import { ILetter } from 'store/slices/state'
import { Letter } from '../Letter/Letter'
import { SquareCell } from '../SquareCell/SquareCell'

interface SquareCellLetterProps extends Pick<ILetter, 'color' | 'value'> {
   id: string
   onClick?: (id: string) => void
}

export const SquareCellLetter: React.FC<SquareCellLetterProps> = ({ children, id, onClick, ...letterProps }) => {
   return (
      <SquareCell id={id} onClick={onClick} mode='shadow'>
         <Letter {...letterProps} />
      </SquareCell>
   )
}
